.card {
  width: 650px;
  height: 500px;
  margin-top: 50px;
  background: #07182E;
  background-size: cover;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  place-content: center;
  place-items: center;
  overflow: hidden;
  border-radius: 20px;
}


.card::before {
  content: '';
  position: absolute;
  width: 150px;
  background-image: linear-gradient(180deg, rgb(0, 183, 255), rgb(255, 48, 255));
  height: 180%;
  animation: rotBGimg 3s linear infinite;
  transition: all 0.2s linear;
}

@keyframes rotBGimg {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.card::after {
  content: '';
  position: absolute;
  background: #07182E;
  ;
  inset: 5px;
  border-radius: 15px;

}

